<template>
  <el-container>
    <el-aside width="200px">
      <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        router
      >
        <el-menu-item index="home">
          <i class="el-icon-goods"></i>
          <span slot="title">首页</span>
        </el-menu-item>


      <div v-if="user">
          <template v-for="(item,index) in user.menus">
          <el-menu-item :index="user.menus_url"   v-if="!item.children"  :key="index">
          <i class="el-icon-goods"></i>
          <span slot="title">{{item.title}}</span>
         </el-menu-item>

         <!-- 有下拉框的 -->
          <el-submenu v-if="item.children" :key="item.title" :index="index+''">
          <template slot="title">
            <i :class="item.icon"></i>
            <span>{{item.title}}</span>
          </template>
          <el-menu-item  v-for="(i,index) in item.children" :key="index" :index="i.url">{{i.title}}</el-menu-item>
        </el-submenu>
        </template>
      </div>

        
        <!-- <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-setting"></i>
            <span>系统设置</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="/index/menu">菜单管理</el-menu-item>
            <el-menu-item index="/index/role">角色管理</el-menu-item>
            <el-menu-item index="/index/manger">管理员管理</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="1">
          <template slot="title">
          <i class="el-icon-goods"></i>
          <span slot="title">商城管理</span>
          </template>
          <el-menu-item index="/index/manger">管理员管理</el-menu-item>
        </el-submenu> -->

      </el-menu>
    </el-aside>
    <el-container>
      <el-header>
        {{rolename}}
        <el-button @click="logout">退出</el-button>
      </el-header>
      <el-main>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>{{$route.name}}</el-breadcrumb-item>
        </el-breadcrumb>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {successAlert} from "../../util/alert"
import {roleInfo} from "../../util/request"
export default {
  data(){
    return {
      rolename:""
    }
  },
  computed: {
    ...mapGetters({
      user:"user"
    }),
  },
  methods: {
    ...mapActions({
      requestMenu: "menu/menuListActions",
      requestUser:"userActions"
    }),
    logout(){
      //点击退出将store中的user数据清空
      this.$confirm("您确定要退出吗?","提示",{
        confirmButtonText:"确定",
        cancelButtonText:"取消",
        type:"warning"
      }).then(()=>{
        successAlert("退出成功！")
        this.requestUser(null)
        this.$router.push("/login")
      }).catch(()=>{
        this.$message({
          type:"info",
          message:"您已取消退出操作!"
        })
      })
     
    },
   
  },
  mounted(){
    roleInfo({id:this.user.roleid}).then(res=>{
      this.rolename=res.data.list.rolename
    })
  }
};
</script>

<style scoped>
.el-aside {
  height: 100vh;
  background: #20222a;
  color: #fff;
}
.el-header {
  background: #ccd4db;
  text-align: right;
  font-size: 14px;
}
.el-button{
  margin: 10px;
}
</style>


